@import url('https://fonts.googleapis.com/css2?family=Ysabeau+SC:wght@200&display=swap');

.App {
}
@keyframes glowing {
  0% {
    text-shadow: 0 0 5px #ffffff;
  }
  25% {
    text-shadow: 0 0 10px #c407fd, 0 0 20px #c407fd;
  }
  50% {
    text-shadow: 0 0 20px #c407fd, 0 0 30px #c407fd;
  }
  75% {
    text-shadow: 0 0 30px #c407fd, 0 0 40px #c407fd;
  }
  100% {
    text-shadow: 0 0 5px #fff;
    color: #c407fd;
  }
}

.main-heading {
  font-size: 10vw;
  text-align: center;
  color: rgb(195, 0, 255);
  font-family: 'Ysabeau SC', sans-serif;
  animation: glowing 3s infinite;
}